.Bg {
	display: inline-block;
	column-count: 25;
	column-gap: 0;
	row-gap: 0px;
	position: fixed;
	line-height: 0px;
	z-index: -1;
}

.Bg img {
	max-width: 100%;
	max-height: 100%;
	opacity: 0;
	animation: fading;
	animation-duration: 2s;
	animation-fill-mode: forwards;
}

@keyframes fading {
	0% {
		opacity: 0;
	}
	100% {
		opacity: 1;
	}
}

@media (max-width: 800px) {
	.Bg {
		column-count: 15;
	}
}
